<template>
  <div class="home">
  <div>
      <div class="qq" v-for="(item, index) in pros" :key="index">
      <van-checkbox class="dr" v-model="item.checked"></van-checkbox>
       <van-card class="dx"
          :num="1"
          :price="item.price"
          :title="item.name"
          :thumb="item.url"
        ></van-card>
        <van-stepper class="wd" v-model="value" min="0" max="5" />
      </div>


   <van-submit-bar
      :price="3050"
      button-text="提交订单"
      @click="onSubmit"
    >
      <van-checkbox v-model="checked">全选</van-checkbox>
    </van-submit-bar>
  </div>
    </div>
</template>

<style scoped lang="less">
  .dx{
  width: 90%;
  float: left;
  }
  .dr{
  width: 10%;
  float: left;
  margin: 40px auto;

  }
  .qq{
  background-color: #fafafa;
  overflow: hidden;
  margin: 10px 0;
  }
  .wd{
   float: right;
  }
</style>


<script>
  export default {

    data() {

      return {
        pros:[1,1,1,1,1],
        queryParams:{count:10},
        checked:false
      };
    },
    methods: {
      onSubmit(){
      // console.log(111)
      },
      value(){
      // console.log(111)
      },
      http(data) {

        return new Promise((resolve, reject) => {

          //发起ajax请求
          //创建ajax对象
          let xhr = new XMLHttpRequest();

          xhr.onreadystatechange = function () {
            if (this.readyState === 4 && this.status === 200) {

              //成功凝固
              resolve(this.response);
            }
          }

          let params = '';

          for (let key in data) {
            params += key + '=' + data[key] + '&';
          }

          params = params.slice(0, -1);

          xhr.open('GET', 'http://api.kangliuyong.com:9002/pros?' + params, true);

          //设置服务器响应的数据类型
          xhr.responseType = 'json';

          xhr.send(null);

        })

      }
    },
    created() {
      let self = this;

      this.http(this.queryParams).then(result => {
        for(let i in result){
          result[i].checked = false;
        }
        self.pros = result;
        // console.log('self.pros ==> ', self.pros);
      })

    },
  };
</script>
