import Vue from 'vue'
import App from './App.vue'
import router from './router'

//从vant组件库导入组件
import {
  Card,
  SubmitBar,
  Checkbox,
  CheckboxGroup,
  Stepper
} from 'vant'

//根据屏幕适配rem
import 'lib-flexible/flexible'

//全局注册组件
Vue
  .use(Card)
  .use(SubmitBar)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Stepper)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
